ulls for the full hd device range.\n\n@media (min-width: @screen-xl-min) {\n  .make-grid(-xl);\n}\n\n@primary-color: #1DA57A;","@import \"../../style/mixins/index\";\n\n// mixins for grid system\n// ------------------------\n.make-row(@gutter: @grid-gutter-width) {\n  position: relative;\n  margin-left: (@gutter / -2);\n  margin-right: (@gutter / -2);\n  height: auto;\n  .clearfix;\n}\n\n.make-grid-columns() {\n  .col(@index) {\n    @item: ~\".@{ant-prefix}-col-@{index}, .@{ant-prefix}-col-xs-@{index}, .@{ant-prefix}-col-sm-@{index}, .@{ant-prefix}-col-md-@{index}, .@{ant-prefix}-col-lg-@{index}\";\n    .col((@index + 1), @item);\n  }\n  .col(@index, @list) when (@index =< @grid-columns) {\n    @item: ~\".@{ant-prefix}-col-@{index}, .@{ant-prefix}-col-xs-@{index}, .@{ant-prefix}-col-sm-@{index}, .@{ant-prefix}-col-md-@{index}, .@{ant-prefix}-col-lg-@{index}\";\n    .col((@index + 1), ~\"@{list}, @{item}\");\n  }\n  .col(@index, @list) when (@index > @grid-columns) {\n    @{list} {\n      position: relative;\n      // Prevent columns from collapsing when empty\n      min-height: 1px;\n      padding-left: (@grid-gutter-width / 2);\n      padding-right: (@grid-gutter-width / 2);\n    }\n  }\n  .col(1);\n}\n\n.float-grid-columns(@class) {\n  .col(@index) { // initial\n    @item: ~\".@{ant-prefix}-col@{class}-@{index}\";\n    .col((@index + 1), @item);\n  }\n  .col(@index, @list) when (@index =< @grid-columns) { // general\n    @item: ~\".@{ant-prefix}-col@{class}-@{index}\";\n    .col((@index + 1), ~\"@{list}, @{item}\");\n  }\n  .col(@index, @list) when (@index > @grid-columns) { // terminal\n    @{list} {\n      float: left;\n      flex: 0 0 auto;\n    }\n  }\n  .col(1); // kickstart it\n}\n\n// lesshint false\n.loop-grid-columns(@index, @class) when (@index > 0) {\n  .@{ant-prefix}-col@{class}-@{index} {\n    display: block;\n    width: percentage((@index / @grid-columns));\n  }\n  .@{ant-prefix}-col@{class}-push-@{index} {\n    left: percentage((@index / @grid-columns));\n  }\n  .@{ant-prefix}-col@{class}-pull-@{index} {\n    right: percentage((@index / @grid-columns));\n  }\n  .@{ant-prefix}-col@{class}-offset-@{index} {\n    margin-left: percentage((@index / @grid-columns));\n  }\n  .@{ant-prefix}-col@{class}-order-@{index} {\n    order: @index;\n  }\n  .loop-grid-columns((@index - 1), @class);\n}\n\n.loop-grid-columns(@index, @class) when (@index = 0) {\n  .@{ant-prefix}-col@{class}-@{index} {\n    display: none;\n  }\n  .@{ant-prefix}-col-push-@{index} {\n    left: auto;\n  }\n  .@{ant-prefix}-col-pull-@{index} {\n    right: auto;\n  }\n  .@{ant-prefix}-col@{class}-push-@{index} {\n    left: auto;\n  }\n  .@{ant-prefix}-col@{class}-pull-@{index} {\n    right: auto;\n  }\n  .@{ant-prefix}-col@{class}-offset-@{index} {\n    margin-left: 0;\n  }\n  .@{ant-prefix}-col@{class}-order-@{index} {\n    order: 0;\n  }\n}\n\n.make-grid(@class: ~'') {\n  .float-grid-columns(@class);\n  .loop-grid-columns(@grid-columns, @class);\n}\n","// mixins for clearfix\n// ------------------------\n.clearfix() {\n  zoom: 1;\n  &:before,\n  &:after {\n    content: \" \";\n    display: table;\n  }\n  &:after {\n    clear: both;\n    visibility: hidden;\n    font-size: 0;\n    height: 0;\n  }\n}\n",".ant-row {\n  position: relative;\n  margin-left: 0;\n  margin-right: 0;\n  height: auto;\n  zoom: 1;\n  display: block;\n}\n.ant-row:before,\n.ant-row:after {\n  content: \" \";\n  display: table;\n}\n.ant-row:after {\n  clear: both;\n  visibility: hidden;\n  font-size: 0;\n  height: 0;\n}\n.ant-row-flex {\n  display: flex;\n  flex-direction: row;\n  flex-wrap: wrap;\n}\n.ant-row-flex:before,\n.ant-row-flex:after {\n  display: flex;\n}\n.ant-row-flex-start {\n  justify-content: flex-start;\n}\n.ant-row-flex-center {\n  justify-content: center;\n}\n.ant-row-flex-end {\n  justify-content: flex-end;\n}\n.ant-row-flex-space-between {\n  justify-content: space-between;\n}\n.ant-row-flex-space-around {\n  justify-content: space-around;\n}\n.ant-row-flex-top {